.onlinePayment__orderInfo {
  &__datas {
    margin-top: 5%;

    & > h1 {
      font-size: 22px;
      color: var(--color-light-blue);
      text-align: center;
    }

    &--context {
      margin-top: 10px;
      border: 2px solid var(--color-light-blue);
      border-radius: 10px;
      padding: 20px;
      position: relative;

      &::before {
        content: "";
        border-left: 20px solid transparent;
        border-top: 15px solid var(--color-light-blue);
        border-right: 20px solid transparent;
        position: absolute;
        top: 100%;
        left: 50%;
        transform: translateX(-50%);
      }

      & > h1 {
        font-size: 18px;
        color: var(--color-dark-grey);
        letter-spacing: 2px;
      }

      &--items {
        height: 100px;
        margin-top: 12px;
        margin-bottom: 24px;
        width: 100%;
        border: 1px solid var(--color-dark-blue);
        display: flex;
        justify-content: space-around;

        & > span {
          line-height: 98px; //100-2(border-top border-bottom)

          & > img {
            width: 100%;
            vertical-align: middle;
            max-height: 97px;
          }
        }

        &--wrap {
          display: flex;
          flex-direction: column;

          &--text {
            & h3 {
              font-size: 12px;
              color: var(--color-dark-grey);
            }

            & p {
              font-size: 10px;
              color: var(--color-light-grey);
            }
          }

          &--total {
            display: flex;
            justify-content: space-around;
            align-items: center;
            margin-top: 7px;

            &--count {
              width: 140px;
              height: 30px;
              display: flex;
              align-items: center;

              & > button {
                width: 30px;
                height: 22px;
                background-color: transparent;
                border: 1px solid var(--color-light-grey);
                color: var(--color-light-grey);
                font-size: 20px;
                cursor: pointer;
              }

              & > span {
                width: 76px;
                height: 22px;
                text-align: center;
                font-size: 12px;
                line-height: 22px;
                border-top: 1px solid var(--color-light-grey);
                border-bottom: 1px solid var(--color-light-grey);
              }
            }

            & > span {
              font-size: 18px;
              color: var(--color-dark-grey);
            }

            & img {
              width: 20px;
              height: 20px;
              cursor: pointer;
            }
          }
        }
      }

      & > span {
        margin-left: 70%;
        display: flex;
        width: 20%;
        justify-content: space-between;
        align-items: center;
        color: var(--color-light-blue);

        & h3 {
          font-size: 18px;
        }

        & h2 {
          font-size: 24px;
        }
      }
    }
  }
}

@media screen and (min-width: 1024px) {
  .onlinePayment__orderInfo {
    &__datas {
      &--context {
        &--items {
          &--wrap {
            flex-direction: row;
            justify-content: space-around;

            &--text {
              width: 50%;

              & h3 {
                font-size: 18px;
                height: 35px;
              }

              & p {
                font-size: 14px;
              }
            }

            &--total {
              width: 30%;

              &--count {
                & > button {
                  height: 30px;
                }

                & > span {
                  height: 30px;
                  line-height: 30px;
                }
              }

              & img {
                width: 40px;
                height: 40px;
              }
            }
          }
        }

        & > span {
          margin-left: auto;
        }
      }
    }
  }
}
